* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Skranji&family=Varela+Round&display=swap");
*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge add Firefox */
* {
  -ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
  scroll-behavior: smooth;
}
:root {
  --color1: #000;
  --color2: #ecf0f1;
  --color3: #ff4757;
  --color4: #ced6e0;
  --fontSkranji: "Skranji";
  --fontOpen: "Open Sans";
  --fontVarela: "Varela Round";
}
.loader {
  width: 100%;
  height: 100vh;
  background-color: var(--color1);
  color: var(--color2);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
}
.spinner {
  position: absolute;
  top: 15px;
  left: 15px;
}
.loader h1 {
  font-family: var(--fontVarela);
  font-size: 50px;
  background-color: #ff4757;
  padding: 0 10px;
}
.white {
  color: var(--color3) !important;
}
.m-r10 {
  margin-right: 20px !important;
}
.wrapper {
  height: auto;
  background-color: var(--color1);
  min-height: 100vh;
}
.nav {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  height: 60px;
  width: 100%;
  color: var(--color2);
  display: grid;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 50px;
  font-family: var(--fontOpen);
  position: relative;
}
.nav h2 {
  font-family: var(--fontVarela);
  font-size: 35px;
  color: var(--color3);
  text-shadow: 0 0 3px var(--color3);
}
.nav ul {
  display: flex;
  list-style: none;
  justify-content: center;
}
nav {
  margin: 15px 0 25px 0;
}
nav ul {
  display: flex;
  list-style: none;
  justify-content: center;
}
.mobile-search {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
  background-color: var(--color1);
}
.mobile-search input {
  height: 30px;
  margin-right: 10px;
  width: 80%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  outline: none;
  padding: 5px 10px;
  font-size: 17px;
  color: var(--color4);
}
.li-style {
  text-decoration: none;
  margin: 0 15px;
  font-size: 18px;
  color: var(--color4);
  position: relative;
}
.li-style::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -19px;
  box-shadow: 0 0 10px var(--color3);
  background-color: var(--color3);
  width: 0%;
  height: 3px;
  border-radius: 15%;
  transition: 0.5s;
}
.active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -19px;
  box-shadow: 0 0 10px var(--color3);
  background-color: var(--color3);
  width: 100%;
  height: 3px;
  border-radius: 15%;
}
.search-inp {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.search-ico:hover {
  transition: 0.5s;
  color: var(--color3) !important;
}
.search-inp input {
  height: 30px;
  margin-right: 10px;
  width: 60%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  outline: none;
  padding: 5px 10px;
  font-size: 17px;
  color: var(--color4);
}
.search-ico {
  color: #ced6e0 !important;
}
.big-ico {
  font-size: 50px !important;
}
.center {
  text-align: center;
}
.modal-box p {
}
.text-modal-search {
  font-family: var(--fontVarela);
  font-size: 20px;
  overflow-wrap: break-word;
  width: 100%;
  color: #000;
}
.badge {
  width: 120px !important;
  height: 30px;
  margin: 0 10px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--color2);
  font-family: var(--fontOpen);
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s;
}
.badge:hover {
  background-color: var(--color3);
  border: none;
  box-shadow: 0 0 5px var(--color3);
}
.badges {
  width: 80%;
  margin: auto;
  max-width: 1536px;
  color: var(--color4);
  font-family: var(--fontVarela);
}
.badges h3 {
  padding: 20px 0;
}
.slick-prev:before,
.slick-next:before {
  color: var(--color3) !important;
  font-family: var(--fontOpen) !important;
}
.css-yuzg60-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected {
  background-color: var(--color3) !important;
  color: var(--color4) !important;
}
.css-1xr9krm {
  color: var(--color2) !important;
}
.css-1xr9krm.Mui-selected {
  background-color: var(--color3) !important;
  color: var(--color4) !important;
}
.css-yuzg60-MuiButtonBase-root-MuiPaginationItem-root {
  color: var(--color2) !important;
}
.css-1v2lvtn-MuiPaginationItem-root {
  color: var(--color4) !important;
}
.css-16y9h98 {
  color: var(--color2) !important;
}
.router {
  width: 100%;
}
.movies {
  padding: 50px 0 0 0;
}
.boxes {
  width: 90%;
  margin: 50px auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  max-width: 1500px;
  grid-column-gap: 20px;
  grid-row-gap: 55px;
  position: relative;
  justify-content: center;
}
.nodata {
  color: #ced6e0;
  font-family: var(--fontVarela);
  text-align: center;
  display: block;
}
.box {
  width: 200px;
  height: 300px;
  margin: auto;
  border-radius: 0px;
  position: relative;
  overflow: hidden;
  color: var(--color4);
  border-width: 1px;
}
.box:hover {
  transition: 0.5s;
  transform: scale(1.2);
  z-index: 110;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.title {
  color: var(--color3);
  font-family: var(--fontVarela);
  font-weight: 600;
  text-align: center;
  width: 95%;
  margin: 0 auto;
  overflow-wrap: break-word;
}
.info-box {
  background-color: rgba(0, 0, 0, 1);
  z-index: 101;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 103;
  backdrop-filter: blur(15px);
  position: absolute;
  opacity: 0;
  transition: 0.5s;
  border: none;
  flex-direction: column;
}
.watch-btn {
  display: none;
}
.info-box:hover {
  opacity: 1;
  cursor: pointer;
}
.info-box:hover .watch-btn {
  display: block;
}
.box img {
  border-radius: inherit;
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  object-fit: contain;
  object-position: center;
  filter: brightness(0.7);
}
.star {
  background-color: rgba(0, 0, 0, 1);
  backdrop-filter: blur(15px);
  position: absolute;
  top: 0px;
  left: 0px;
  color: var(--color2);
  font-family: var(--fontVarela);
  padding: 0 10px;
  font-weight: 500;
  font-size: 15px;
}
.star2 {
  position: absolute;
  bottom: 0px;
  right: 0;
  color: var(--color2);
  font-family: var(--fontVarela);
  background-color: var(--color3);
  padding: 0 5px;
  font-size: 15px;
  /* backdrop-filter: blur(15px); */
  z-index: 101;
}
.loader2 {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #000;
  z-index: 105;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color4);
  font-family: var(--fontVarela);
}
.watch-btn {
  font-family: var(--fontVarela);
  margin-top: 10px;
  width: 140px;
  border-radius: 0px;
  text-align: center;
  color: var(--color4);
  border: 2px solid rgba(255, 255, 255, 0.4);
  transition: 0.2s;
}
.watch-btn:hover {
  border: 2px solid var(--color3);
  background-color: var(--color3);
}
.irouter {
  width: 100%;
  max-width: 1700px;
  margin: auto;
}
.show {
  width: 100%;
  height: 100%;
  overflow: scroll;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 110;
  background-color: #000;
}
.backdrop {
  width: 100%;
  height: 450px;
  object-fit: cover;
  filter: blur(10px) brightness(0.8);
}
.for-big-screen {
  max-width: 1600px;
  margin: auto;
  height: 100%;
  width: 100%;
}
.top-screen {
  height: 500px;
  width: 100%;
  position: relative;
}
.infos {
  position: absolute;
  bottom: -50px;
  left: 75px;
  color: var(--color2);
  font-family: var(--fontVarela);
  display: flex;
}
.infos h1 {
  background-color: var(--color1);
  padding: 0 10px;
  display: inline-block;
}
.infos p {
  width: 560px;
  background-color: var(--color1);
  overflow-wrap: break-word;
  line-height: 1.7;
  padding: 5px 10px;
  margin-top: 10px;
}
.info-img {
  width: 250px;
  height: 350px;
  object-fit: cover;
  margin-right: 50px;
}
.badges2 {
  margin: 10px 0;
  display: flex;
  background-color: none !important;
}
.badges2 p {
  width: auto;
  background-color: var(--color3);
  color: var(--color2);
  backdrop-filter: blur(10px);
  text-align: center;
  padding: 0 5px;
}
.badge2 {
  display: flex;
  flex-wrap: wrap;
  height: 30px;
  align-items: center;
  margin: 0 5px;
}

footer {
  height: 300px;
  width: 100%;
  background-image: url(./footer-bg.jpg);
  background-size: cover;
  font-family: var(--fontVarela);
  padding: 0 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
footer h1 {
  color: #ff4757;
  padding: 15px 0;
}
footer p {
  background-color: #000;
  display: inline-block;
  width: 250px;
  color: #ecf0f1;
  margin: 5px 0;
  padding: 5px;
}
.vote {
  background-color: #000 !important;
  margin-right: 5px;
  text-align: center;
  border-radius: 0 10px 10px 0 !important;
}
.badge-link {
  font-family: var(--fontVarela);
  color: var(--color4);
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
}
.badge .active {
  background-color: #ff4757 !important;
}

.slider2 {
  width: 95%;
  margin: auto;
  text-align: center;
}
.slider2 iframe {
  width: 100%;
  height: 100%;
  /* border: 1px solid rgba(255, 255, 255, 0.1); */
  border-radius: 5px;
}
.margin-top {
  margin-top: 100px;
}
.slide-item {
  width: 85% !important;
  aspect-ratio: 16/9;
  position: relative;
}
.main-slider2 {
  width: 90%;
  margin: auto;
}
.white2 {
  font-family: var(--fontVarela);
  color: #ff4757;
  text-align: center;
  margin: 25px 0;
}
.fake-load-slider2 {
  width: 100%;
  height: 100%;
  background-color: var(--color1);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid var(--color1);
}
.slick-dots li button:before {
  color: white !important;
  opacity: 1 !important;
}
.slick-dots li.slick-active button:before {
  color: #ff4757 !important;
  opacity: 1;
}
.back-btn {
  position: fixed;
  background-color: #ff4757;
  color: #ecf0f1;
  top: 10px;
  left: 10px;
  width: 120px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 105;
  text-align: center;
  font-family: var(--fontVarela);
  cursor: pointer;
}
.back-btn p {
  margin-left: 5px;
  padding-top: 2px;
}
.mobile-link {
  margin: 0 10px;
  font-size: 15px;
  font-family: var(--fontVarela);
  text-decoration: none;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--color4);
  padding: 3px 7px;
  border-radius: 10px;
}
.mobile {
  margin: 25px auto 10px auto;
  width: 85%;
  display: none;
}
.search-inp2 {
  display: none;
}

@media (max-width: 1100px) {
  .nav h2 {
    font-size: 25px;
  }
  .li-style {
    font-size: 15px;
  }
  .search-inp input {
    width: 210px;
    font-size: 15px;
  }
  .infos {
    width: 95%;
    left: 2.5%;
  }
  .infos h1 {
    font-size: 25px;
  }
  .infos p {
    font-size: 15px;
    padding: 2px 10px;
    overflow-wrap: break-word;
    width: 80%;
  }
  .badges2 p {
    width: auto;
  }
}
@media (max-width: 1024px) {
  .nav {
    grid-template-columns: repeat(2, 1fr);
  }
  .search-inp input {
    width: 60%;
    font-size: 15px;
    min-width: 200px;
  }
  .nav ul {
    display: none;
  }
  .badges h3 {
    font-size: 17px;
  }
  .movies {
    padding-top: 10px;
  }
  .boxes {
    margin: 30px auto;
  }
  .mobile {
    display: block;
  }
  .active {
    background-color: #ff4757 !important;
  }
  .active::after {
    display: none;
  }
  .infos p {
    width: 95%;
  }
  .badges2 p {
    width: auto;
    flex-shrink: 0;
  }
  .badges2 {
    flex-wrap: wrap;
  }
  .info-img {
    margin-right: 20px;
  }
}
@media (max-width: 700px) {
  .infos {
    flex-direction: column;
    margin: 50px auto 0 auto;
    position: static;
    width: 95%;
  }
  .top-screen {
    height: auto;
  }
  .info-img {
    margin: 15px auto;
    width: 95%;
    object-position: center;
  }
  .backdrop {
    display: none;
  }
  .slide-item {
    width: 100% !important;
    aspect-ratio: 16/11;
    border: 1px solid rgba(255, 255, 255, 0.4);
  }
  .slider2 {
    width: 90%;
  }
  .margin-top {
    margin-top: 50px;
  }
  .badges {
    width: 75%;
  }
  .mobile {
    width: 95%;
    margin: 25px auto;
  }
}
@media (max-width: 500px) {
  .search-inp {
    display: none;
  }
  .search-inp2 {
    display: flex;
    justify-content: flex-end;
  }
  .nav {
    padding: 0 10px;
  }
  .boxes {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0;
  }
  .box:hover {
    transform: none;
  }
  .box {
    height: 250px;
    width: 45%;
    margin: 10px 5px;
  }
}
